html{
  width: 100%;
  height: 100%;
}
body {
  text-align: center;
  width: 100%;
  height: 100%;
  background-color: #1F1831
  ;
  color:white;
  font-family: 'Raleway', sans-serif;


  #app{
    width: 100%;
    height: 100%;
  }
  .navbar {
    background: #21374B;
    color: #E7DACB;
    height: 50px;
  }
  h1 {
    font-size: 40px;
    margin-top: 30px;
  }

  h1,h2,h3,h4{
    font-family: 'Lato', sans-serif;
  }
}

button{
  background-color: #D5A583;
  color:white;
  border: none;
  box-sizing: border-box;
  padding: 10px 30px;
  font-weight: bold;
  border-radius:5px;
  font-size:20px;
}

button:hover{
  background-color: #A05455
}

.app-layout{
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: 1fr 200px 1fr;
    grid-template-rows: 100px 1fr 100px;
    grid-template-areas: "header header header" "points aggregate polygons" "footer footer footer";
    align-items: center;
}

.inputfile {
	width: 0.1px;
	height: 0.1px;
	opacity: 0;
	overflow: hidden;
	position: absolute;
	z-index: -1;
}


.inputfile + label {
  background-color: #D5A583;
  color:white;
  border: none;
  box-sizing: border-box;
  padding: 10px 30px;
  font-weight: bold;
  border-radius:5px;
  font-size:20px;
}

.inputfile:focus + label,
.inputfile + label:hover {
  background-color: #A05455;
  cursor: pointer; /* "hand" cursor */

}

.header { grid-area: header; }

.header h1{
  margin:70px 0px 0px 0px;
  padding:0px;
  font-size:70px;
}
.header h3{
  font-family: 'Raleway', sans-serif;
  margin:0px;
  margin-top:10px;
  font-weight: 100;
}

.points { grid-area: points; }

.polygons { grid-area: polygons; }

.footer { grid-area: footer; }

.aggregate{grid-area: aggregate;}

.columns-to-aggregate{
  list-style: none;
  margin: 0px;
  padding: 0px;
  display: grid;
  grid-template-columns: repeat(auto-fill, 200px);
  grid-column-gap: 20px;
  justify-content: center;
}

.point-loading{
  display:flex;
  flex-direction: column;
  align-items: center;
}
.columns-to-aggregate li{
  display:flex;
  flex-direction: row;
  justify-content: space-between;
}

.progress-bar-outer{
  height: 30px;
  border:2px solid #796D9F;
  width:100%;
  box-sizing: border-box;
  padding:5px;
}

.progress-bar-inner{
  background-color: #B3A0BB;
  height:100%;
  width:0%;
}

